export function userDataReducer(state = initialState, action: { type: any; payload: any; }) {
  switch (action.type) {
    case actions.SET_CUSTOMER_DATA:
      return {
        ...state,
        Customer: action.payload
      };
    case actions.SET_CURRENT_COMPETITION_DATA:
      return {
        ...state,
        CurrentCompetitionData: action.payload
      };

    case actions.SET_COMPETITIONS:
      return {
        ...state,
        Competitions: action.payload
      };
    case actions.SET_MANAGE_ANIMATIOM_INDEX:
      return {
        ...state,
        ManageAnimationIndex: action.payload,
      };
    case actions.SET_COMPETITIONS:
      return {
        ...state,
        Competitions: action.payload
      };
    default:
      return state;
  };
}

export const store = createStore(userDataReducer); // Create the Redux store with rootReducer and middleware