export const SET_CUSTOMER_DATA = 'SET_CUSTOMER_DATA';
export const SET_CURRENT_COMPETITION_DATA = 'SET_CURRENT_COMPETITION_DATA';
export const SET_COMPETITIONS = 'SET_COMPETITIONS'
export const SET_MANAGE_ANIMATIOM_INDEX = 'SET_MANAGE_ANIMATIOM_INDEX';

export const setCustomerData = (data: any) => ({
  type: SET_CUSTOMER_DATA,
  payload: data,
});

export const setCurrentCompetitionData = (data: any) => ({
  type: SET_CURRENT_COMPETITION_DATA,
  payload: data,
});

export const setCompetitionsArr = (data: any) => ({
  type: SET_COMPETITIONS,
  payload: data,
});
export const setManageAnimationsIndex = (data: any) => ({
  type: SET_MANAGE_ANIMATIOM_INDEX,
  payload: data,
});

