import React, { useState } from 'react';
import { HubConnectionBuilder } from '@microsoft/signalr';

const SignalRComponent = () => {
    const [tvId, setTvId] = useState('');
  
    const handleJoinTv = () => {
        connection.invoke("joinRoom", tvId)
            .catch(err => console.error("Error joining TV room: ", err));
    };
    
    const connection = new HubConnectionBuilder()
        .withUrl("https://dev.qwan.co.il/signalr/hubs")
        .build();

    connection.on("broadcastMessage", (type, message) => {
        console.log(`Received message of type ${type}: ${message}`);
        // Update your React component state with the received message
    });

    connection.start()
        .then(() => console.log("SignalR Connected"))
        .catch(err => console.error("SignalR Connection Error: ", err));

    return (
        <div>
            <input 
                type="text" 
                value={tvId} 
                onChange={(e) => setTvId(e.target.value)} 
                placeholder="TV id"
            />
            <button onClick={handleJoinTv}>Join TV</button>
        </div>
    );
};

export default SignalRComponent;