import './App.css';
import Init from './components/Init/Init';

import qwanLogo from './assets/QwanLogo.svg';
import NextCompetition from './components/NextCompetition/NextCompetition';
import SignalRComponent from './services/tempService';

function App() {

  const preloadMultipleVideos = (videoUrls: string[]) => {
    videoUrls.forEach((videoUrl) => {
      const video = document.createElement('video');
      video.src = videoUrl;
      video.load();
    });
  };
  const url = process.env.PUBLIC_URL || '';
  console.log('url', url);

  const videoUrls = [`${url}/assets/nextCompetition/A.mp4`, `${url}/assets/nextCompetition/B.mp4`,
  `${url}/assets/nextCompetition/C.mp4`, `${url}/assets/nextCompetition/D.mp4`,
  `${url}/assets/nextCompetition/E.mp4`, `${url}/assets/nextCompetition/F.mp4`,
  `${url}/assets/backgroundTemplates/backgroundA.mp4`, `${url}/assets/backgroundTemplates/backgroundB.mp4`,
  `${url}/assets/backgroundTemplates/backgroundC.mp4`, `${url}/assets/backgroundTemplates/backgroundD.mp4`,
  `${url}/assets/backgroundTemplates/backgroundE.mp4`, `${url}/assets/backgroundTemplates/backgroundF.mp4`];
  
  preloadMultipleVideos(videoUrls);

  return (
    <>
      {/* <Init /> */}
      {/* <NextCompetition competition={{
        Id: '22',
        EndDate: new Date(Date.now() + 3 * 60000),
        Template: 'A'
      }} nextScreenDisplayTime={7000} /> */}
      <SignalRComponent/>
      {/* <img src={qwanLogo} id='qwanLogo' /> */}
    </>
  );
}

export default App;
